import styled from "styled-components";

import LinkButton from "../Button/LinkButton";
import { colors } from "../styleguide";
import { Typography } from "../Typography";

import { StyledToastProps, ToastVariant } from "./Toast.types";
import ToastNotification from "./ToastNotification";

export const toastVariantBackgroundColor: Record<ToastVariant, string> = {
	Default: colors.black,
	Warning: colors.yellow,
	Error: colors.red,
	Validation: colors.teal,
};

export const DismissButtonWrapper = styled.div<StyledToastProps>`
	padding-left: 5px;
	& > button {
		background: transparent;
		color: ${({ variant }) => variant === "Default" ? colors.white : colors.black};
		&:hover {
			background: transparent;
		}
	}
`;

export const IconWrapper = styled.span`
	vertical-align: middle;
	margin-right: 10px;
	margin-top: 5px;
`;

export const TrailingLinkButton = styled(LinkButton) <StyledToastProps>`
	height: 100%;
	background: transparent;
	color: ${({ variant }) => variant === "Default" ? colors.white : colors.black};
`;

export const StyledToast = styled.div<StyledToastProps>`
    position: relative;
	height: 48px;
	width: fit-content;
	padding: 14px 6px 14px 14px;
	box-sizing: border-box;
	display: flex;
	align-items:center;
	border-radius: 8px;
	transform: translateY(0%);
	background-color: ${({ backgroundColor, variant }) =>
		backgroundColor || (variant && toastVariantBackgroundColor[variant])};
	color: ${({ variant }) => variant === "Default" ? colors.white : colors.black};
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.08),
		0 4px 4px rgba(0, 0, 0, 0.08),
		0 8px 8px rgba(0, 0, 0, 0.08),
		0 16px 16px rgba(0, 0, 0, 0.08),
		0 32px 32px rgba(0, 0, 0, 0.08);
	margin: auto;
	margin-bottom: 32px;
`;

export const StyledToastTypography = styled(Typography)`
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	overflow: hidden;
	text-overflow: ellipsis;
`;

export const NotificationBodyTypography = styled(Typography)`
  white-space: pre-wrap;
`;

export const ToastContainer = styled.div`
  position: absolute;
  bottom: 20px;
  width: 100vw;
  left:0;
  right:0;
`;

export const NotificationToast = styled(ToastNotification)`
  z-index: ${({ $zIndex }) => $zIndex ?? "999"};
`;
